@font-face {
  font-family: "opensans";
  src: url("/fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "opensans_bold";
  src: url("/fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "opensans_semibold";
  src: url("/fonts/opensanssemibold.woff2") format("woff2"), url("/fonts/opensanssemibold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "robotobold";
  src: url("/fonts/Robotobold.woff2") format("woff2"), url("/fonts/Robotobold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: "opensans", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  max-width: 1170px;
  display: block;
  margin: 0 auto; }

.button {
  padding: 14px 15px;
  box-sizing: border-box;
  background-color: transparent;
  text-transform: uppercase;
  cursor: pointer; }

.hide {
  display: none; }

@media (max-width: 980px) {
  .wrap {
    padding: 0 10px; } }

.header {
  padding: 50px 0;
  max-height: 120px; }

.header__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.header__logo {
  text-decoration: none;
  color: inherit;
  font-size: 20px; }

@media (max-width: 768px) {
  .header__wrap {
    justify-content: center; } }

.nav__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0; }

.nav__item {
  margin-left: 40px;
  font-size: 20px; }

.nav__link {
  text-decoration: none;
  color: inherit; }

@media (max-width: 768px) {
  .nav__list {
    margin-top: 20px;
    justify-content: center; }
  .nav__item {
    margin-top: 10px; } }

.main {
  background: url("/img/bgmain.png") center no-repeat;
  opacity: 0.9;
  background-color: #000;
  background-size: cover; }

.main__wrap {
  text-align: center;
  padding-top: 200px;
  padding-bottom: 70px;
  color: #fff; }

.main__title {
  font-size: 60px;
  line-height: 1;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 25px; }

.main__text {
  font-size: 22px;
  font-weight: normal;
  line-height: 22px; }

.main__btns {
  margin-top: 100px;
  margin-bottom: 100px; }

.main__button {
  color: white;
  margin-right: 25px;
  border: 3px solid #f8f8f8;
  transition-duration: 2s;
  transition-property: border-color; }
  .main__button:hover {
    border-color: #00897b; }

.main__button--green {
  border: 3px solid transparent;
  color: #fff;
  background-color: #00897b;
  transition-duration: 0.5s;
  transition-property: background-color; }
  .main__button--green:hover {
    background-color: #1fb3a4; }

.main__arrow {
  text-decoration: none;
  display: block;
  margin: 0 auto;
  width: 84px;
  height: 42px;
  cursor: pointer;
  animation-name: animateArrow;
  animation-duration: 2s;
  animation-iteration-count: infinite; }

@media (max-width: 768px) {
  .main__title {
    font-size: 40px; }
  .main__text {
    font-size: 16px; } }

@media (max-width: 480px) {
  .main__title {
    font-size: 30px; }
  .main__text {
    font-size: 12px; }
  .main__btns {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px; }
  .main__button,
  .main__button--green {
    margin: 20px; } }

@keyframes animateArrow {
  0% {
    transform: translateY(0px); }
  50% {
    transform: translateY(30px); }
  100% {
    transform: translateY(0px); } }

.about__wrap {
  padding-top: 125px;
  padding-bottom: 125px;
  text-align: center; }

.about__title {
  font-size: 30px;
  font-family: "opensans_bold";
  margin-bottom: 35px; }

.about__text {
  font-size: 15px;
  max-width: 620px;
  display: block;
  margin: 0 auto; }

.about__block {
  margin-top: 125px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.about__item {
  flex-basis: 24%; }

.about__world {
  width: 62px;
  height: 62px; }

.about__transfer {
  width: 70px;
  height: 60px; }

.about__head {
  width: 57px;
  height: 63px; }

.about__item-name {
  font-family: "opensans_semibold"; }

@media (max-width: 768px) {
  .about__item {
    flex-basis: 100%;
    margin: 20px; } }

@-webkit-keyframes shadow-drop-2-bottom {
  0% {
    -webkit-transform: translateZ(0) translateY(0);
    transform: translateZ(0) translateY(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0); }
  100% {
    -webkit-transform: translateZ(50px) translateY(-12px);
    transform: translateZ(50px) translateY(-12px);
    -webkit-box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35);
    box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35); } }

@keyframes shadow-drop-2-bottom {
  0% {
    -webkit-transform: translateZ(0) translateY(0);
    transform: translateZ(0) translateY(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0); }
  100% {
    -webkit-transform: translateZ(50px) translateY(-12px);
    transform: translateZ(50px) translateY(-12px);
    -webkit-box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35);
    box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35); } }

.services {
  background-color: #e4fdf9; }

.services__wrap {
  padding-top: 173px;
  padding-bottom: 120px; }

.services__title {
  text-align: center;
  font-size: 30px;
  margin-bottom: 35px; }

.services__subtitle {
  max-width: 620px;
  text-align: center;
  margin: 0 auto; }

.services__block {
  margin-top: 112px;
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap; }

.services__item {
  width: 25%;
  padding: 40px;
  padding-top: 58px;
  margin-bottom: 30px;
  text-align: center;
  background-color: #fff;
  transition-duration: 500ms;
  transition-property: color, border-color, background-color; }
  .services__item:hover {
    animation: shadow-drop-2-bottom 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    background-color: #00897b;
    color: #fff; }
    .services__item:hover .services__btn {
      border-color: #fff;
      color: #fff; }

.services__img-icon {
  width: 56px;
  height: 62px;
  fill: red; }

.services__item-name {
  font-family: "opensans_semibold";
  font-size: 20px;
  margin-top: 38px; }

.services__item-text {
  font-size: 15px;
  margin-top: 26px;
  margin-bottom: 44px; }

.services__btn {
  padding: 20px 14px;
  font-size: 20px;
  border: 2px solid #212121; }

@media (max-width: 980px) {
  .services__block {
    justify-content: space-around; }
  .services__item {
    padding: 20px;
    padding-top: 58px;
    width: 40%;
    margin-bottom: 20px; } }

@media (max-width: 768px) {
  .services__item {
    width: 100%;
    margin-bottom: 20px; } }

.works__wrap {
  padding-top: 130px; }

.works__title {
  text-align: center;
  margin-bottom: 40px; }

.works__subtitle {
  text-align: center;
  max-width: 620px;
  margin: 0 auto; }

.works__block {
  margin-top: 110px; }

.works-btn {
  border: 2px solid #00897b;
  margin-right: 20px;
  transition-duration: 500ms;
  transition-property: background-color, color; }
  .works-btn:hover {
    background-color: #00897b;
    color: #fff; }
  .works-btn:last-child {
    margin-right: 0; }

.works__buttons {
  display: flex;
  align-items: center;
  justify-content: center; }

.works-item {
  width: 370px;
  height: 370px;
  margin-bottom: 30px; }

.works-img {
  width: 100%; }

.works__grid {
  display: flex;
  margin-top: 110px;
  justify-content: space-between;
  flex-wrap: wrap; }
